'use strict';

const wall = document.querySelector('.wall');
const spider = document.querySelector('.spider');

const centerSpiderX = spider.clientHeight / 2;
const centerSpiderY = spider.clientWidth / 2;

document.addEventListener('click', (e) => {
  const clickLocation = e.target.closest('.wall');

  if (!clickLocation) {
    return;
  }

  const topGap = wall.offsetTop + wall.clientTop + centerSpiderX;
  const leftGap = wall.offsetLeft + wall.clientLeft + centerSpiderY;

  // Set limits
  const topLimit = wall.clientHeight - spider.clientHeight;
  const leftLimit = wall.clientWidth - spider.clientWidth;

  let y = e.clientY - topGap;
  let x = e.clientX - leftGap;

  // hmmm... i saw this somewhere...
  // Ensure why is at least 0, if y is smaller than 0.
  y = Math.max(y, 0);
  // Ensure to not exceed limit.
  y = Math.min(y, topLimit);

  x = Math.max(x, 0);
  x = Math.min(x, leftLimit);

  spider.style.top = `${y}px`;
  spider.style.left = `${x}px`;
});
